import { Component } from '@angular/core';
import { NavController, NavParams, LoadingController, AlertController, ModalController, ActionSheetController } from 'ionic-angular';
import { Api } from '../../providers/api';
import moment from 'moment';
import { ProductSearchPage } from '../product-search/product-search';
import { Printer } from '@ionic-native/printer';
@Component({
  selector: 'page-seller',
  templateUrl: 'seller.html',
})
export class SellerPage {
  charge = {
    residence_id: null,
    user_id: null,
    amount: 0,
    quantity: 1,
  }
  items = [];
  residences = [];
  residents = [];
  mode = "restricted";
  toPrint;
  invoices_history = [];
  constructor(public navCtrl: NavController, public navParams: NavParams, public loading: LoadingController, public alert: AlertController, public modal: ModalController, public actionsheet: ActionSheetController, public printer: Printer, public api: Api) {
  }

  ionViewDidLoad() {
    this.api.storage.get('invoices_history')
      .then((history) => {
        if (history) {
          this.invoices_history = history;
        }
      })
      .catch(console.error)
    if (this.mode !== 'restricted') {
      this.items.push({ concept: '', amount: 0, quantity: 0, });
    }
    this.getResidences();
  }

  getResidences() {
    this.api.get('residences')
      .then((data: any) => {
        this.residences = data;
      })
      .catch(console.error)
  }

  getResidents() {
    if (!this.charge.residence_id) {
      return;
    }
    this.api.get('residences/' + this.charge.residence_id + '?with[]=residents')
      .then((data: any) => {
        this.residents = data.residents;
      })
      .catch(console.error)
  }

  clear() {
    this.charge = {
      residence_id: null,
      user_id: null,
      amount: 0,
      quantity: 1,
    }
    if (this.mode !== 'restricted') {
      this.items = [{
        concept: '',
        amount: 0,
        quantity: 0,
      }];

    }
    else {
      this.items = [];
    }
    this.residents = [];
  }

  addItem() {
    if (this.mode == 'restricted') {
      this.findProduct();
    }
    else {
      this._addItem();
    }
  }

  _addItem(item = { concept: '', amount: 0, quantity: 0, }) {
    this.items.push(item);
  }

  findProduct() {
    var modal = this.modal.create(ProductSearchPage, {})
    modal.present();
    modal.onDidDismiss((data, role) => {
      if (role !== 'cancel') {
        console.log(data, role);
        this._addItem({ concept: data.name, amount: data.price, quantity: 1 })
      }
    });
  }

  removeItem(index) {
    this.items.splice(index, 1);
  }

  proccess() {
    var procesing = 0;
    var loading = this.loading.create({
      content: this.api.trans('__.procesando') + procesing + '  de ' + this.items.length,
    });
    loading.present();
    this.items.forEach(element => {
      this.api.post('charges', {
        'residence_id': this.charge.residence_id,
        'concept': element.concept + "(x" + element.quantity + ")",
        amount: element.amount * element.quantity,
        month: moment().month() + 1,
        year: moment().year(),
        type: "unique",
      })
        .then(() => {
          loading.setContent(this.api.trans('__.procesando') + ++procesing + '  de ' + this.items.length);
          if (procesing == this.items.length) {
            if (this.charge.user_id) {
              this.sendPush("Se ha generado un nuevo cargo a su factura", this.charge.user_id);
            }

            loading.dismiss();
            this.complete();
          }
        })
        .catch((err) => {
          console.error(err);
          loading.dismiss();
          this.alert.create({
            title: "ERROR",
            message: JSON.stringify(err)
          }).present();
        })
    });

  }

  proccessWithInvoice() {
    var loading = this.loading.create({
      content: this.api.trans('__.procesando'),
    });
    loading.present();
    this.api.post('invoices', {
      user_id: this.charge.user_id,
      residence_id: this.charge.residence_id,
      items: this.items,
      type: 'normal',
      date: (new Date()).toISOString().substring(0, 10),

    })
      .then((invoice: any) => {
        this.api.post(`invoices/${invoice.id}/Payment`, {})
          .then((data) => {
            if (this.charge.user_id) {
              this.sendPush("Compra Realizada!", this.charge.user_id);
            }
            this.goPrint(invoice);
            loading.dismiss();
          })
          .catch((err) => {
            console.error(err);
            loading.dismiss();
            this.alert.create({
              title: "ERROR",
              message: JSON.stringify(err)
            }).present();
          });
      })
      .catch((err) => {
        console.error(err);
        loading.dismiss();
        this.alert.create({
          title: "ERROR",
          message: JSON.stringify(err)
        }).present();
      });
  }

  goPrint(invoice) {
    var user = this.residents.find((res) => {
      return res.id == this.charge.user_id;
    });
    this.toPrint = { invoice: invoice, user: user };
    this.saveInvoice(this.toPrint);
    setTimeout(() => {
      this.printer.print(document.getElementById('toPrint'), { name: 'invoice' })
        .then(() => {
          this.complete();
          this.toPrint = null;
        })
        .catch((err) => {
          console.error(err);
        });

    }, 1000);
  }

  saveInvoice(invoice) {
    this.invoices_history.push(invoice);
    this.invoices_history.slice(this.invoices_history.length - 500)
    this.api.storage.set('invoices_history', this.invoices_history);
  }

  complete() {
    this.alert.create({
      message: this.api.trans('literals.done'),
      buttons: ["OK"]
    }).present();
    this.clear();
  }

  canProccess() {
    var valid = true;
    this.items.forEach((item) => {
      if (!(item.amount > 0 && item.quantity > 0 && item.concept.length > 0))
        valid = false;
    });
    return this.charge.residence_id != null
      && this.items.length > 0
      && valid;
  }

  total(invoice = null) {
    var items;
    if (invoice == null)
      items = this.items
    else
      items = invoice.items
    var total = 0;
    items.forEach((item) => {
      total += item.amount * item.quantity;
    });
    return total;
  }

  sendPush(message, user_id = this.charge.user_id) {
    if (!user_id) return;
    this.api.post('push/' + user_id + '/notification', { message: message })
      .then(() => {

      })
      .catch((error) => {
        console.error(error);
      })
  }

  actions() {
    this.actionsheet.create({
      title: this.api.trans('__.que desea hacer?'),
      buttons: [
        {
          text: this.api.trans('__.agregar a la siguiente factura'),
          icon: 'paper',
          cssClass: 'icon-primary',
          handler: () => {
            this.proccess();
          }
        },
        {
          text: this.api.trans('__.facturar ahora'),
          icon: 'print',
          cssClass: 'icon-secondary',
          handler: () => {
            this.proccessWithInvoice();
          }
        },
        {
          text: this.api.trans('crud.cancel'),
          role: 'cancel',
          icon: 'close',
          cssClass: 'icon-light',
          handler: () => {
            console.log('Cancel clicked');
          }
        }
      ]
    }).present();
  }
}



